@import url(https://fonts.googleapis.com/css2?family=Cabin+Condensed&display=swap);
body{margin:0}ul{margin:0;padding:0;list-style:none}.App{height:100vh;display:flex;justify-content:center;margin:0 auto;padding:60px 0;background:#ffc1c2;background:linear-gradient(135deg, #ffc1c2 0%, #9c71ca 100%)}.wrap{width:17.5rem;height:28.125rem;position:relative;background-color:white;border-radius:.625rem}.nav{display:flex}.nav__tab{height:3.75rem;display:flex;justify-content:center;align-items:center;width:50%;background-color:#fafafa;box-shadow:inset 3px 0 5px -5px #c1c1c1,inset -3px 0 5px -5px #c1c1c1,inset 0 -3px 5px -5px #c1c1c1}.nav__tab--active{background-color:#fff;box-shadow:none}.nav__tab:first-child{-webkit-border-top-left-radius:.625rem;-moz-border-radius-topleft:.625rem}.nav__tab:last-child{-webkit-border-top-right-radius:.625rem;-moz-border-radius-topright:.625rem}.btn{width:3.75rem;height:3.75rem;position:absolute;bottom:0;left:50%;transform:translate(-50%, 50%);border-radius:50%;background-color:#febc70;border:none;outline:none;font-size:2.5rem;font-weight:bold;color:#fff;transition-duration:0.3s}.btn:hover{box-shadow:inset 0 0 0 3.75rem #8F829E}.todo-list{padding:1.875rem 1.25rem}.todo{margin-bottom:1.25rem}.todo__text{white-space:nowrap;overflow:hidden;text-overflow:ellipsis;transition:opacity 400ms ease-in}.todo__label{position:relative;display:flex;align-items:center;padding-left:1.5625rem;font-family:"Cabin Condensed",sans-serif;line-height:1.25rem;color:#656565;cursor:pointer}.todo__label::before{content:'';display:inline-block;height:0.9375rem;width:0.9375rem;position:absolute;z-index:1;margin-left:-25px;border:solid 1px #c1c1c1;border-radius:.3125rem;opacity:0.7}.todo__label::after{content:'';display:inline-block;height:0.9375rem;width:0.9375rem;position:absolute;left:0.25rem;top:-0.15rem;z-index:2;background:url(data:image/png;base64,) no-repeat;opacity:0;transition:opacity 300ms}.todo__text--completed{opacity:0.5;text-decoration:line-through}input[type="checkbox"]{display:none}input[type="checkbox"]:checked+label::after{opacity:1}

